class Sequence extends React.Component {
  constructor(props) {
    super(props);
    this.state = { step: 1 };
  }

  componentDidMount() {
    this.stepID = setInterval(() => this.step(), 500);
  }

  componentWillUnmount() {
    clearInterval(this.stepID);
  }

  step() {
    const prev = this.state.step;
    const next = prev == 8 ? 1 : prev + 1;
    this.setState({
      step: next
    });
  }

  render() {
    return (
      <div>
        <p>{this.state.step}</p>
        <Track step={this.state.step} />
        <Track step={this.state.step} />
        <Track step={this.state.step} />
        <Track step={this.state.step} />
      </div>
    );
  }
}

export default Sequence;
